
.row__poster{
    width: 100%;
    object-fit: contain;
    max-height: 100px;
    margin-right: 10px;
    transition: 450ms;
}

.row__posters{
    display: flex;
    overflow-x: scroll;
    overflow-y: hidden;
    padding: 20px;

}

.row__poster:hover{
    transform: scale(1.08);
}


.row__posterLarge{
    max-height: 250px;
}
.row__posterLarge:hover{
    transform: scale(1.09);
}



.row__posters::-webkit-scrollbar{
    display: none;
}

.row{
    margin-left: 20px;
    color: white;
}
.row h1{
    font-size: 1.4rem;
    padding: 20px;
}